// Progress

.ant-progress-type-primary,
.ant-progress-type-secondary,
.ant-progress-type-success,
.ant-progress-type-info,
.ant-progress-type-warning,
.ant-progress-type-danger {
  .ant-progress-bg {
    -webkit-backface-visibility: hidden;
    -webkit-transform: translateZ(0) scale(1, 1);
  }
}

.ant-progress-type-primary {
  .ant-progress-bg {
    background-color: rgba($primary, 0.85);
  }
}
.ant-progress-type-secondary {
  .ant-progress-bg {
    background-color: rgba($secondary, 0.85);
  }
}
.ant-progress-type-success {
  .ant-progress-bg {
    background-color: rgba($success, 0.85);
  }
}
.ant-progress-type-info {
  .ant-progress-bg {
    background-color: rgba($info, 0.85);
  }
}
.ant-progress-type-warning {
  .ant-progress-bg {
    background-color: rgba($warning, 0.85);
  }
}
.ant-progress-type-danger {
  .ant-progress-bg {
    background-color: rgba($danger, 0.85);
  }
}
